import {AxiosInstance} from "axios";
import {DomainSearchDTO, EmailContactDTO, EmailContactVerificationDTO} from "@backend-lib";

export class HunterApi {
  static getInstance: (api: AxiosInstance) => HunterApi;

  constructor(private api?: AxiosInstance) {
    if(!api){
      throw new Error("No axios instance provided")
    }
  }

  async findEmail(name: string, domain: string): Promise<any> {
    const url = `/hunter/find-email?name=${encodeURIComponent(name)}&domain=${encodeURIComponent(domain)}`;

    const response = await this.api!.get(url);
    return response.data;
  }

  async searchDomain(domain: string): Promise<any> {
    const url = `/hunter/search-domain?domain=${encodeURIComponent(domain)}`;

    const response = await this.api!.get(url);
    return response.data;
  }

  async getDomainSuggestions(domain: string): Promise<any> {
    const url = `/hunter/domain-suggestions?domain=${encodeURIComponent(domain)}`;

    const response = await this.api!.get(url);
    return response.data;
  }

  async getLastDomainsSuggestions(): Promise<any> {
    const url = `/hunter/last-domains-suggestions`;
    const response = await this.api!.get(url);
    return response.data;
  }

  async getLastSearches(): Promise<EmailContactDTO[]> {
    const url = `/hunter/last-searches`;
    const response = await this.api!.get(url);
    return response.data;
  }

  async getLastVerifications(): Promise<EmailContactVerificationDTO[]> {
    const url = `/hunter/last-verifications`;
    const response = await this.api!.get(url);
    return response.data;
  }

  async getLastDomainSearches(): Promise<DomainSearchDTO[]> {
    const url = `/hunter/last-domains`;
    const response = await this.api!.get(url);
    return response.data;
  }

  async verifyEmail(email) {
    const url = `/hunter/verify-email?email=${encodeURIComponent(email)}`;
    const response = await this.api!.get(url);
    return response.data;
  }

  async findByDomain(domain) {
    const url = `/hunter/find-email-domain?domain=${encodeURIComponent(domain)}`;
    const response = await this.api!.get(url);
    return response.data;
  }

  async getSearches() {
    const url = `/hunter/searches`;
    const response = await this.api!.get(url);
    return response.data;
  }
}

HunterApi.getInstance = function (api: AxiosInstance) {
  return new HunterApi(api);
}
