import {AxiosInstance} from "axios";
import {PlanDTO} from "../../../backend-lib/src";

export class StripeApi {
  signupInProgress = false;
  static getInstance: (api?: AxiosInstance) => StripeApi;

  constructor(private api?: AxiosInstance) {
    if(!api){
      throw new Error("No axios instance provided")
    }
  }

  /**
   * Retrieve the Stripe public key
   */
  public async config() {
    const r = (await this.api!.get("/checkout/config")).data
    return r
  }

  public async getWelcomePlans():Promise<PlanDTO[]> {
    const r = (await this.api!.get("/checkout/welcome-plans")).data
    return r?.plans
  }

  /**
   * Retrieve the Stripe session id
   * @param priceNickname
   */
  public async checkout(priceNickname: string) {
      const r = (await this.api!.get("/checkout/session/"+priceNickname)).data?.url
      return r
  }

  /**
   * Retrieve the Stripe checkout url (replace the session because it's not working on MV3)
   * @param priceNickname
   */
  public async url(priceNickname: string) {
    const r = (await this.api!.get("/checkout/url/"+priceNickname)).data?.url
    return r
  }


  async getBillingUrl() {
    return (await this.api!.get("/checkout/billing-url")).data?.url
  }
}

StripeApi.getInstance = (api?: AxiosInstance) => {
  return new StripeApi(api!)
}
