import {storage} from "./utilities";
import axios from "axios";
import Config from "./config";
import {MeDTO} from "@backend-lib";


export const Account = {
    // Search if there isn't any account with this email already saved and create it
    // if not. The token is null untile the Google account is connected.
    //
    createAccountEntry: async function (email:string) {
        const value = (await storage().get({"accounts": []})) || {accounts:[]} as any
        const accounts = value.accounts.filter((a:any) => a.email == email);
        if (accounts.length == 0) {
            value.accounts.push({
                email: email,
                token: null,
                activated: true,
                ask_google_connect_wait_until: 0,
                tracking_domain: "",
                current_plan_name: "",
                messages_count: 0,
                billings_url: "",
                customer_portal_url: "",
                remaining_trackings_pdfs: 0
            });
            await storage().set({"accounts": value.accounts})
        }
        return value.accounts.find((a:any) => a.email == email);
    },
    // Modify any attribute of the account defined by its email address.
    //
    editAccountAttribute: async function (email:string, attribute:string, data:any) {
        const value =  (await storage().get({"accounts": []})) || {accounts:[]} as any
        let account = value.accounts.filter((a:MeDTO) => a.email == email)[0];
        if(!account){
            account = await Account.createAccountEntry(email.toLowerCase())
        }
        // We remove the entry
        const index = value.accounts.findIndex((a:any) => a.email == email)
        if(index) {
            value.accounts.splice(index, 1);
        }

        // We modify the value
        account[attribute] = data;

        const accounts = value.accounts.filter((a:any) => a.email == email);
        if (accounts.length == 0) {
            // We add the account back to the list
            value.accounts.push(account);
        }

        // We save the list
        await storage().set({"accounts": value.accounts});

    },

    // Get account data from its email address. If there is no account, it returns null.
    //
    getAccountData: async function (email:string) {

        const value =  (await storage().get({"accounts": []})) || {accounts:[]} as any
        let account = value.accounts.filter((a:any) => a.email == email)[0];

        if (typeof account == "undefined") {
            account = null
        }
        return account
    },
     loadMe: async function (token:string) {
        const response = await axios.get(
            Config.hunter_domain + "/api/me?token=" + token)
        const responseData = response.data
        const email = responseData?.data?.email.toLowerCase()
        // We get the tracking domain name that should be used by this account
        // We make sure to use several domain names for the tracking to manage
        // the risk. The domain to be used is decided on server side so we can
        // update quickly if there is a deliverability issue with one domain
        // name.
        await Account.createAccountEntry(email)
        await Account.editAccountAttribute(
            email,
            "tracking_domain",
            responseData.data?.tracking_domain,
        );
        await Account.editAccountAttribute(
            email,
            "id",
            responseData.data?.id,
        );
        return (responseData)
    },
    // Get accounts data. If there is no account, it returns null.
    //
    getAccountsData: async function () {
        const value =  (await storage().get({"accounts": []})) || {accounts:[]} as any
        return (value.accounts);
    },
    isPaying: function (planName) {
        return planName && planName.toLowerCase() !== "free"
    },
    isInvited: function (planName) {
        return planName === "invited"
    },
    isFree: function (planName) {
        return !Account.isPaying(planName)
    },
    isDuo(planName: string) {
        return planName.includes("duo")
    },
    isMulti(planName: any) {
        return planName.includes("multi")
    },
    isSignatureEnabled(createdAt: Date){
        return new Date(createdAt) > new Date("2023-06-06T00:00:00.000Z")
    }
}
