import {AxiosInstance} from "axios";
import {MeDTO} from "@backend-lib";

export class AuthApi{
  static getInstance: (api: AxiosInstance) => AuthApi;
  constructor(private api?:AxiosInstance){
    if(!api){
      throw new Error("No axios instance provided")
    }
  }
  public async me():Promise<MeDTO>{
    return (await this.api!.get("/auth/me")).data
  }
}
AuthApi.getInstance = (api:AxiosInstance)=>{
  return new AuthApi(api)
}
