import {AxiosInstance} from "axios";
import {MeDTO} from "@backend-lib";

export class OrgaApi {
    static getInstance: (api: AxiosInstance) => OrgaApi;

    constructor(private api?: AxiosInstance) {
        if (!api) {
            throw new Error("No axios instance provided")
        }
    }

    /**
     * Add ignored email
     * @param email
     */
    public async ignoreEmail(email: string): Promise<MeDTO> {
        return (await this.api!.put(`/orga/ignore-sign-in/${email}`))
    }

    /**
     * Remove ignored email
     * @param email
     */
    public async removeIgnoredEmail(email: string): Promise<MeDTO> {
        return (await this.api!.delete(`/orga/ignore-sign-in/${email}`))
    }


    async unMuteEmails() {
        const url = `/users/updateMutedEmails/unMute`;
        const response = await this.api!.patch(url, {});
        return response.data;
    }

    async muteEmails() {
        const url = `/users/updateMutedEmails/mute`;
        const response = await this.api!.patch(url, {});
        return response.data;
    }
}

OrgaApi.getInstance = (api: AxiosInstance) => {
    return new OrgaApi(api)
}
