import {isDevMode} from "./utilities";
// Default tracking
if (typeof window !== "undefined") {
  (window as any).hunter_tracking_activated = true;
}

// hunter_domain is the domain we're using to communicate to with the Hunter
// application.

var hunter_domain = "https://mailtracker.hunter.io";
if (isDevMode && isDevMode()){
  hunter_domain = "https://mailtracker.ngrok.io"
}
if (typeof window !== "undefined") {
  if ((window as any).storyBook) {
    hunter_domain = "https://mailtracker.ngrok.io"
  }
}

var hunter_public_pdf_regexp = new RegExp(/\/p\/pdf\/([a-f\d-]+)/g)

export default {
  hunter_domain,
  hunter_public_pdf_regexp
}
