import storageHelper from "./storageHelper";
import md5 from "md5";

export function generateRandomID() {
  var text = "";
  var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";

  for (var i = 0; i < 20; i++)
    text += possible.charAt(Math.floor(Math.random() * possible.length));

  return text;
}

/**
 * Compliance with storybook
 * @returns {chrome.storage|Storage}
 */
class FakeStorage {
  data = {};

  constructor() {
    this.data = {};
  }

  async set(keyValuePair) {
    this.data = keyValuePair;
  }

  async get(key) {
    const value = this.data[key];
      return (value);
  }

  remove(key) {
    delete this.data[key];
  }

  clear() {
    this.data = {};
  }
}

const fk = new FakeStorage();

export function storage() {
  if (chrome.storage) {
    return storageHelper;
  } else {
    return fk;
  }
}

export function isDevMode() {
  if (chrome.runtime) {
    return (chrome.runtime.getManifest().name.startsWith("DEV: "));
  } else {
    return true;
  }
}

export function capitalize(string) {
  return string.charAt(0).toUpperCase() + string.slice(1);
}

// Replace all the URLs by links, except the links that are alredy present.
export function replaceURLWithHTMLLinks(html, replace) {
  html = ">" + html + "<";
  html = html.replace(/>([^<>]+)(?!<\/a)</g, function(match, txt) {
    let regex = /(\b(https?|ftp|file):\/\/[-A-Z0-9+&@#\/%?=~_|!:,.;]*[-A-Z0-9+&@#\/%=~_|])/ig;
    replace = "<a href='$1'>$1</a>";
    return ">" + txt.replace(regex, replace) + "<";
  });

  return html.substring(1, html.length - 1);
}

// Sort an object by date
(function() {
  if (typeof Object.defineProperty === "function") {
    try {
      Object.defineProperty(Array.prototype, "sortBy", { value: sb });
    } catch (e) {
    }
  }
  //@ts-ignore
  if (!Array.prototype.sortBy) Array.prototype.sortBy = sb;

  function sb(f) {
    // @ts-ignore
    for (var i = this.length; i;) {
      // @ts-ignore
      var o = this[--i];
      // @ts-ignore
      this[i] = [].concat(f.call(o, o, i), o);
    }
    // @ts-ignore
    this.sort(function(a, b) {
      for (var i = 0, len = a.length; i < len; ++i) {
        if (a[i] != b[i]) return a[i] < b[i] ? -1 : 1;
      }
      return 0;
    });
    // @ts-ignore
    for (var i = this.length; i;) {
      // @ts-ignore
      this[--i] = this[i][this[i].length - 1];
    }
    // @ts-ignore
    return this;
  }
})();

// Count the usage
export async function countUsage() {
  const value = await storage().get({ "usage_count": 0 })
    let new_value = value.usage_count + 1;
    await storage().set({ "usage_count": new_value });
}


