export function getCountryName(countryCode) {
  if (isoCountries.hasOwnProperty(countryCode)) {
    return isoCountries[countryCode];
  } else {
    return countryCode;
  }
}

export function removeSearchParam(param) {
  // Create a URL object from the current page URL
  const url = new URL(window.location.href);

  // Get the search parameters from the URL
  const searchParams = new URLSearchParams(url.search);

  // Remove the specified search parameter
  searchParams.delete(param);

  // Update the URL object with the modified search parameters
  url.search = searchParams.toString();

  // Update the browser's address bar without reloading the page
  window.history.replaceState(null, "", url.toString());
}

export function getGravatarUrl(recipients) {
  const url = recipients.filter(recipient=>recipient.email)
    .map(recipient=> {
      const hash = md5(recipient.email);
      const gravatarUrl = `http://gravatar.com/avatar/${hash}?s=256&d=nil`;
      return gravatarUrl;
    })?.[0];
  return url
}

export function getGravatarUrlReplacement(recipients) {
  const replacement = recipients.filter(recipient=>recipient.email)
    .map(recipient=> {
      const name = recipient.name||recipient.email;
      return name?.[0]?.toUpperCase()+name?.[1]?.toUpperCase()
    })?.[0];
  return replacement
}
