import {AxiosInstance} from "axios";
import {
  CampaignDTO, CampaignLimitationDTO, CampaignLinkDTO, CampaignRecipientDTO,
  CampaignSchedulerConfigDTO, EmailTemplateDTO,
  MailMergerHeaderDTO,
  MailMergerRecipientDTO,
} from "@backend-lib";
import {notification} from "antd";
export class MailMergerAPI {
  static getInstance: (api: AxiosInstance) => MailMergerAPI;

  constructor(private api?: AxiosInstance) {
    if (!api) {
      throw new Error("No axios instance provided")
    }
  }

  async loadRecipients(csv: string): Promise<{ recipients: MailMergerRecipientDTO[], headers: MailMergerHeaderDTO[] }> {
    const url = `/mailmerger/loadRecipients`;
    const response = await this.api!.post(url, {csv});
    return response.data;
  }

  async generateCaptureLinkUrl(slug:string) {
    const url = `/mailmerger/campaigns/links/${slug}`;
    const response = await this.api!.put (url);
    return response.data?.url;
  }

  async getCaptureLink(slug:string): Promise<CampaignLinkDTO> {
    const url = `/public/mailmerger/campaigns/links/${slug}`;
    const response = await this.api!.get(url);
    return response.data?.link;
  }

  async addRecipientsToCampaign(campaignId:string,slug:string, recipients:{email:string,rgpd:boolean}[]): Promise<any> {
    try {
      const url = `/public/mailmerger/campaigns/${campaignId}/links/${slug}/recipients`;
      const response = await this.api!.post(url, {recipients});
      return response.data;
    } catch (e:any) {
      if(e.response.status == 409) {
        notification.error({message: "This email is already registered"})
      } else if (e.response.data.error) {
        notification.error({message: e.response.data.error})
      } else {
        notification.error({message: "Unknown error, try to refresh the page"})
      }
      throw e;
    }
  }

  async generateDefaultCaptureLink(formFields): Promise<CampaignLinkDTO> {
    const url = `/public/mailmerger/campaigns/links`;
    const response = await this.api!.put(url,{formFields});
    return response.data?.link;
  }

  async getCampaignLimitationOnFreePlan(campaignId: string): Promise<CampaignLimitationDTO> {
    // get 'campaign/:campaignId/free/limitations'
    const url = `/mailmerger/campaigns/${campaignId}/free/limitations`;
    const response = await this.api!.get(url);
    return response.data;
  }

  async getCampaigns(): Promise<CampaignDTO[] > {
    const url = `/mailmerger/campaigns`;
    const response = await this.api!.get(url);
    return response.data;
  }

  async getRecipients(campaignId) {
    const url = `/mailmerger/campaigns/${campaignId}/recipients`;
    const response = await this.api!.get(url);
    return response.data;
  }

  async createCampaign(campaign: CampaignDTO): Promise<{ campaign: CampaignDTO }> {
    const url = `/mailmerger/campaigns`;
    const response = await this.api!.post(url, {
      campaign: {
        ...campaign,
        status: "draft"
      }
    });
    return response.data;
  }

  async getDefaultSmartPilotConfig(): Promise<CampaignSchedulerConfigDTO> {
    const url = `/mailmerger/campaigns/defaultSmartPilotConfig`;
    const response = await this.api!.get(url);
    return response.data;
  }

  async updateCampaign(campaign: CampaignDTO, emailTemplate: EmailTemplateDTO): Promise<{ campaign: CampaignDTO }> {
    const url = `/mailmerger/campaigns/${campaign.id}`;
    const response = await this.api!.patch(url, {
      campaign,
      emailTemplate
    });
    return response.data;
  }

  async updateCampaignSchedulerConfig({campaignId, schedulerConfig}: {campaignId: string, schedulerConfig: CampaignSchedulerConfigDTO}): Promise<void> {
    const url = `/mailmerger/campaigns/config/${campaignId}`;
    const response = await this.api!.post(url, {
      schedulerConfig
    });
    return response.data;
  }
  async loadCampaign(campaignId: string): Promise< CampaignDTO> {
    const url = `/mailmerger/campaigns/${campaignId}`;
    const response = await this.api!.get(url);
    return response.data;
  }

  async loadCampaignByDraft(draftId: string): Promise< CampaignDTO> {
    const url = `/mailmerger/campaigns/by-draft/${draftId}`;
    const response = await this.api!.get(url);
    return response.data;
  }

  async deleteCampaign(campaignId: string) {
    const url = `/mailmerger/campaigns/${campaignId}`;
    const response = await this.api!.delete(url);
    return response.data;
  }

  async emailPreview({recipient, subjectTemplate, htmlBodyTemplate}: {
    recipient: MailMergerRecipientDTO,
    htmlBodyTemplate: string,
    subjectTemplate: string
  }): Promise<{ htmlBodyContent: string, subjectContent: string }> {
    const url = `/mailmerger/emailPreview`;
    const response = await this.api!.post(url, {
      recipient,
      subjectTemplate,
      htmlBodyTemplate
    });
    return response.data;
  }
  async sendTestEmail({recipient, subjectTemplate, htmlBodyTemplate}: {
    recipient: MailMergerRecipientDTO,
    htmlBodyTemplate: string,
    subjectTemplate: string
  }): Promise<{ htmlEmailContent: string }> {
    const url = `/mailmerger/sendTestEmail`;
    const response = await this.api!.post(url, {
      recipient,
      subjectTemplate,
      htmlBodyTemplate
    });
    return response.data;
  }

  async testScopes() {
    const url = `/mailmerger/testScopes`;
    const response = await this.api!.put(url, {});
    return response.data;
  }

  async pauseCampaign(campaignId: string) {
    const url = `/mailmerger/pauseCampaign/${campaignId}`;
    const response = await this.api!.post(url, {});
    return response.data;
  }
  async resumeCampaign(campaignId: string) {
    const url = `/mailmerger/resumeCampaign/${campaignId}`;
    const response = await this.api!.post(url, {});
    return response.data;
  }
  async launchCampaign(campaignId: string):Promise<{recipients?:CampaignRecipientDTO[],link?: CampaignLinkDTO}>  {
    const url = `/mailmerger/launchCampaign/${campaignId}`;
    const response = await this.api!.post(url, {});
    return response.data;
  }
}

MailMergerAPI.getInstance = function (api: AxiosInstance) {
  return new MailMergerAPI(api);
}
