import {AxiosInstance} from "axios";

export class GoogleApi {
  signupInProgress = false;
  static getInstance: (api?: AxiosInstance) => GoogleApi;

  constructor(private api?: AxiosInstance) {
    if(!api){
      throw new Error("No axios instance provided")
    }
  }

  public async signup(code: string) {
    if (!this.signupInProgress) {
      this.signupInProgress = true
      try {
        const r = (await this.api!.post("/google/signup", {code})).data
        return r
      } catch (e) {
        throw e
      } finally {
        this.signupInProgress = false
      }
    } else {
      return
    }
  }
}

GoogleApi.getInstance = (api?: AxiosInstance) => {
  return new GoogleApi(api!)
}
